import React, { Component } from 'react';
import Checkbox from "./Checkbox";
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import './App.css';
import distanceData from './distances';

class App extends Component {
  constructor(props) {
    super(props);

    this.locations = Object.keys(distanceData).sort();
    this.state = {
      checkboxes: this.locations.reduce(
        (options, option) => ({
          ...options,
          [option]: false
        }),
        {}
      ),
      start: this.locations[0],
      solution: []
    };
  }

  selectAllCheckboxes = isSelected => {
    Object.keys(this.state.checkboxes).forEach(checkbox => {
      this.setState(prevState => ({
        checkboxes: {
          ...prevState.checkboxes,
          [checkbox]: isSelected
        }
      }));
    });
  };

  selectAll = () => this.selectAllCheckboxes(true);
  deselectAll = () => this.selectAllCheckboxes(false);

  handleCheckboxChange = changeEvent => {
    const { name } = changeEvent.target;

    this.setState(prevState => ({
      checkboxes: {
        ...prevState.checkboxes,
        [name]: !prevState.checkboxes[name]
      }
    }));
  };

  handleFormSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();

    this.setState(prevState => ({
      solution: this.computeSolution()
    }));
  };

  updateStart = location => {
    this.setState(prevState => ({
      start: location.value
    }));
  };

  computeSolution = () => {
    let shortestPath = [];
    let selectedLocations = Object.keys(this.state.checkboxes).filter(checkbox => this.state.checkboxes[checkbox] && checkbox !== this.state.start);
    let availableLocations = [...selectedLocations];

    var currentLocation = this.state.start;
    for (var i = 0; i < selectedLocations.length; i++) {
      var nextLocation = "";
      var minDistance = 999;
      var distanceObj = Object.keys(distanceData[currentLocation])
        .filter(key => availableLocations.includes(key))
        .reduce((obj, key) => {
          obj[key] = distanceData[currentLocation][key];
          return obj;
        }, {});

      for (const [target, distance] of Object.entries(distanceObj)) {
        if (distance < minDistance) {
          nextLocation = target;
          minDistance = distance;
        }
      }

      shortestPath.push(nextLocation);
      currentLocation = nextLocation;
      availableLocations.splice(availableLocations.indexOf(nextLocation), 1);
    }

    return shortestPath;
  };

  render() {
    return (
      <div>
        <h1>Traveling Freighter</h1>

        <h2>Select your starting location</h2>
        <Dropdown options={this.locations} value={this.state.start} onChange={this.updateStart} placeholder="Select a start location" />

        <h2>Select the locations of boxes you have to deliver</h2>
        <form onSubmit={this.handleFormSubmit}>
          {this.locations.map(name =>
            <Checkbox
              label={name}
              isSelected={this.state.checkboxes[name]}
              onCheckboxChange={this.handleCheckboxChange}
              key={name}
            />
          )}

          <div className="form-group mt-2">
            <button
              type="button"
              className="btn btn-outline-primary mr-2"
              onClick={this.selectAll}
            >
              Select All
            </button>
            <button
              type="button"
              className="btn btn-outline-primary mr-2"
              onClick={this.deselectAll}
            >
              Deselect All
            </button>
            <button type="submit" className="btn btn-primary">
              Compute your path!
            </button>
          </div>
        </form>

        <h2>Your Path</h2>
        <table>
          {this.state.solution.map(location => <tr>{location}</tr>)}
        </table>
      </div>
    );
  }
}

export default App;
